@import url('https://fonts.googleapis.com/css2?family=Asap+Condensed:ital,wght@1,600&family=Mukta:wght@500&display=swap');



.App {
  font-family: 'Mukta', sans-serif;
  text-align: center;
}

/* Styling for links with link class */
.link {
  text-decoration: none;
  color: #00364A;
}

/*Landing*/

.landing-wrapper {
  width: 80vw;
  margin: auto
}



/*Main*/
.main-wrapper {
  margin-top: 160px;
}

/*footer*/
.footer-wrapper {
  position: fixed;
  bottom: 0;
  width: 100vw;
  background-color: #31BED4;
  height: 25px;
  margin: 0
}

.contributor-wrapper {
  margin-right: 15px;
  display: flex;
  justify-content: flex-end;
  align-items: center;
}

.contributor {
  margin-right: 10px;
  margin-top: 2px;
  display: flex;
  align-items: flex-start;
}



.spacer {
  height: 25px
}

/*Register*/
.register-form {
  display: flex;
  flex-direction: column;
  align-items: center;
}